import { ConvertToPluginActionCreators, Node } from '@ali/4ever-cangjie';
import { IHrASL } from '@ali/4ever-utils';

export const HR_INSERT = Symbol('HR_INSERT');
export const HR_UPDATE = Symbol('HR_UPDATE');

export function insertHr(data: any) {
  return {
    type: HR_INSERT,
    payload: data,
  };
}

export interface IUpdateHrPayload {
  node: Node | null;
  data: IHrASL;
}

export function updateHr(payload: IUpdateHrPayload) {
  return {
    type: HR_UPDATE,
    payload,
  };
}


export const actions = {
  insertHr,
  updateHr,
};

/** @deprecated */
export const DEPRECATED_ACTIONS = {
  HR_INSERT,
  HR_UPDATE,
  insertHr,
  updateHr,
};

declare module '@ali/4ever-cangjie' {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
}
